@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  color: #011c40;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}
@media (max-width: 1023px) {
  .header {
    box-shadow: initial;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 500px;
  }
}

.header__bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  min-width: 100%;
  height: 650px;
  min-height: 100%;
}
@media (max-width: 1023px) {
  .header__bg-wrapper {
    height: 638px;
  }
}
@media (max-width: 767px) {
  .header__bg-wrapper {
    left: 50%;
    transform: translateX(-50%);
    width: 758px;
    height: 568px;
  }
}

.header__background {
  display: block;
  width: 100%;
  height: 650px;
  min-height: 100%;
  object-fit: cover;
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .header__background {
    height: 638px;
  }
}
@media (max-width: 767px) {
  .header__background {
    height: 568px;
  }
}
@media (max-width: 374px) {
  .header__background {
    object-position: 20px;
  }
}
@media (max-width: 767px) {
  .header__background--nojs {
    height: 100%;
  }
}

.header__container {
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 20px;
  }
}

.header__nav {
  margin-left: auto;
}
@media (max-width: 767px) {
  .header__nav {
    margin: 0;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 33px;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .nav__list {
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .nav__list {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}

.nav__link {
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .nav__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.nav__link.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav__link {
    padding: 10px;
    padding-bottom: 8px;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

.logo__link {
  display: block;
}

.logo__image {
  display: block;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .logo__image {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__image {
    width: 42px;
    height: 22px;
  }
}

.burger {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .burger--open {
    position: fixed;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    border-left: 35px solid transparent;
    background-color: rgba(0, 43, 102, 0.5);
  }
  .burger--open .burger__image {
    fill: #011c40;
  }
  .burger--open .burger__close {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: -2;
  }
  .burger--open .burger__container {
    width: 100%;
    height: 100vh;
    background-color: #f9fbfd;
    z-index: -2;
    overflow: auto;
  }
  .burger--open .burger__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    padding: 75px 30px 15px 15px;
  }
  .burger--open .burger__toggle {
    position: absolute;
    top: 25px;
    right: 15px;
    cursor: pointer;
  }
  .burger--open .burger__toggle::before, .burger--open .burger__toggle::after {
    content: "";
    position: absolute;
    top: 5px;
    left: 9px;
    width: 18px;
    height: 2px;
    background-color: #011c40;
  }
  .burger--open .burger__toggle::before {
    transform: rotate(45deg);
  }
  .burger--open .burger__toggle::after {
    transform: rotate(-45deg);
  }
  .burger--open .burger__list {
    display: grid;
  }
}

.burger__close {
  display: none;
}

@media (max-width: 767px) {
  .burger__logo {
    align-self: flex-start;
  }
}

.burger__toggle {
  display: none;
}
@media (max-width: 767px) {
  .burger__toggle {
    position: relative;
    display: block;
    width: 24px;
    height: 14px;
    margin-left: auto;
    border: none;
    background-color: transparent;
    align-self: center;
    z-index: 2;
    cursor: pointer;
  }
}
.burger__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
}
@media (max-width: 767px) {
  .burger__toggle--nojs {
    display: none;
  }
}

@media (max-width: 767px) {
  .burger__list {
    display: none;
  }
}
@media (max-width: 767px) {
  .burger__list--nojs {
    display: grid;
    padding-left: 20px;
  }
}
@media (max-width: 767px) {
  .burger__list--nojs .nav__link {
    color: #f9fbfd;
  }
}

